<template>
  <div class="home" v-if="!loading">
    <FilterNav @updateFilter="updateFilter" />
    <div v-if="filteredProjects.length">
      <div v-for="project in filteredProjects" :key="project.id">
        <SingleProject
          :project="project"
          @delete="handleDelete"
          @complete="handleComplete"
        />
      </div>
    </div>
  </div>
  <div v-else>Loading...</div>
</template>

<script>
import SingleProject from "../components/SingleProject.vue";
import FilterNav from "../components/FilterNav.vue";
import { ref, onMounted, computed } from "vue";
import getProjects from "../composables/getProjects";

export default {
  name: "HomeView",
  components: {
    SingleProject,
    FilterNav,
  },
  setup() {
    const { posts, error, load } = getProjects();
    const projects = ref([]);
    const current = ref("all");
    const loading = ref(false);

    onMounted(async () => {
      loading.value = true;
      await load();
      loading.value = false;
      projects.value = posts.value;
    });

    const handleDelete = (id) => {
      console.log("del", id);
      projects.value = projects.value.filter((i) => i.id !== id);
    };

    const handleComplete = (id) => {
      const project = projects.value.find((i) => i.id === id);
      console.log("com", id);
      if (project) {
        project.completed = !project.completed;
      }
    };

    const updateFilter = (by) => {
      current.value = by;
    };

    const filteredProjects = computed(() => {
      if (current.value === "completed") {
        return projects.value.filter((i) => i.completed);
      } else if (current.value === "ongoing") {
        return projects.value.filter((i) => !i.completed);
      }
      return projects.value;
    });

    return {
      projects,
      current,
      handleDelete,
      handleComplete,
      updateFilter,
      filteredProjects,loading
    };
  },
};
</script>
<style>
.home {
  max-width: 600px;
  margin: 20px auto;
  background: white;
  border-radius: 5px;
  box-shadow: 5px 5px 15px -5px rgba(0, 0, 0, 0.3);
}
</style>
