<template>
  <div class="project" :class="{ completed: project.completed }">
    <div class="actions" >
      <p @click="showDetails = !showDetails">{{ project.title }}</p>
      <div class="icons">
        <router-link :to="{ name: 'EditProject', params: { id: project.id } }">
          <span class="material-icons">edit</span>
        </router-link>
        <span class="material-icons" @click="deleteProject">delete</span>
        <span class="material-icons tick" @click="finish">done</span>
      </div>
    </div>
    <div class="details" v-if="showDetails">
      <p>{{ project.details }}</p>
    </div>
  </div>
</template>

<script>
import { projectFirestore } from "@/firebase/config";
export default {
  props: ["project"],
  data() {
    return {
      showDetails: false,
    };
  },
  methods: {
    async deleteProject() {
      await projectFirestore
        .collection("projects")
        .doc(this.project.id)
        .delete();
        this.$emit("delete", this.project.id);
    },
    async finish() {
      try {
        const projectRef = projectFirestore
          .collection("projects")
          .doc(this.project.id);

        // Update the project's completion status in Firestore
        await projectRef.update({
          completed: !this.project.completed,
        });

        this.$emit("complete", this.project.id);
      } catch (error) {
        console.error(
          "Error updating project completion status in Firestore:",
          error
        );
      }
    },
  },
};
</script>

<style>

.project {
  min-height: 70px;
  border-bottom: 1px solid #f0eeee;
  border-left: 5px solid #e91e63;
  }


.project.completed {
  border-left: 5px solid #00bcd4;
}
.project.completed .tick {
  color: #00bcd4;
}
.actions p {
  cursor: pointer;
  flex: 1;
  font-size: 21px;
  font-weight: 200;
  color: #1c5b72;
}
.actions {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
}
.icons span {
  margin-left: 10px;
  cursor: pointer;
  font-size: 24px;
  color: #bbb;
  transition: 0.3s;
}
.icons span:hover {
  color: #777;
}
.details{
  padding: 20px;
}
</style>
