      ><h1 class="date">{{ getDate() }}</h1></router-link
    >
    <router-link :to="{ name: 'AddProjects' }"><button>+</button></router-link>
  </div>
</template>
<script>
export default {
  methods: {
    getDate() {
      const today = new Date();
      const options = {
        weekday: "long",
        day: "numeric",
        month: "long",
      };
      return today.toLocaleDateString("en-us", options);
    },
  },
};
</script>

