<template>
  <form @submit.prevent="update">
    <label>Title</label>
    <input type="text" v-model="title" required />
    <label>Details</label>
    <textarea v-model="details" required></textarea>
    <button>Update Project</button>
  </form>
</template>

<script>
import { ref } from "vue";
import getProject from "../composables/getProject";
import { projectFirestore } from "@/firebase/config";
import { useRouter } from 'vue-router';

export default {
  props: ["id"],
  setup(props) {
    const title = ref("");
    const details = ref("");
    const router = useRouter();
    const { post, error, load } = getProject(props.id);
    const fetchPost = async () => {
      await load();
      title.value = post.value.title;
      details.value = post.value.details;
    };
    const update = async() => {
      await projectFirestore.collection("projects").doc(props.id).update({
        title: title.value,
        details: details.value,
      });

      router.push("/");
    };

    fetchPost();

    return {
      title,
      details,
      fetchPost,
      update,
    };
  },
};
</script>

<style>
button {
  cursor: pointer;
}
</style>
